<template>
  <div class="main">
    <Header
      :transparent.sync="transparent"
      :mobile-icon-active.sync="mobileIconActive"
    />
    <StartScreen>
      <span slot="subtitle">
        С нами поиск грузов
        <br />
        стал проще
      </span>
      <nuxt-link
        slot="link"
        :to="'/how-it-works/carrier/1/'"
        class="md-raised outline link-button"
      >
        Как это работает?
      </nuxt-link>
      <div slot="video" class="video_wrapper">
        <video preload="auto" loop muted autoplay class="video">
          <source src="~@/assets/video/main/2.mp4" type="video/mp4" />
        </video>
      </div>
      <img
        slot="poster"
        src="/img/poster3.jpg"
        class="poster"
        alt="Сервис поиска грузов для перевозчиков MoreCargo"
      />
    </StartScreen>
    <Owners type="carrier" :list="list" />
    <PromoBlock promo-title="поиск грузов по России" />
    <Steps :steps="steps" />
    <BringFriend />
    <FAQ :faq-list="faqList" />
    <Questions gray type="carrier" />
    <Footer />
  </div>
</template>

<script>
import socialImg from '@/assets/img/social-banner_twitter.jpg';
import vkSocialImg from '@/assets/img/social-banner_vk.jpeg';
import Header from '@/components/Header';
import StartScreen from '@/components/start-screen/StartScreen';
import BringFriend from '@/components/BringFriend';
import Owners from '@/components/Owners';
import PromoBlock from '@/components/PromoBlock';
import Steps from '@/components/Steps';
import Questions from '@/components/Questions';
import FAQ from '@/components/FAQ';
import Footer from '@/components/Footer';

import { carrierItems as list } from '@/assets/enums/slides';
import { carrierSteps as steps } from '@/assets/enums/steps';

import axios from 'common/helpers/httpService';

export default {
  head: {
    title: 'Поиск грузов для перевозки по России - найти груз онлайн',
    meta: [
      { name: 'url', content: 'https://morecargo.ru/carriers' },
      {
        name: 'description',
        content:
          'Поиск грузов по России в сервисе MoreCargo. Бонусы при регистрации в сервисе, прозрачная система тарифов и документооборота. Найти груз быстро и выгодно с сервисом MoreCargo'
      },
      { property: 'og:url', content: 'https://morecargo.ru/carriers/' },
      { property: 'og:type', content: 'website' },
      {
        property: 'og:title',
        content:
          '6000 рублей за регистрацию? Легко! Используй бонусы для оплаты сервиса грузоперевозок'
      },
      {
        property: 'og:image',
        content: `https://morecargo.ru${socialImg}`
      },
      {
        property: 'vk:image',
        content: `https://morecargo.ru${vkSocialImg}`
      },
      { property: 'fb:app_id', content: '595435127624032' },
      {
        property: 'og:site_name',
        content: 'https://morecargo.ru/carriers'
      },
      { property: 'og:locale', content: 'ru_RU' },
      { property: 'twitter:card', content: 'summary_large_image' },
      { name: 'twitter:site', content: 'https://morecargo.ru/carriers' },
      { name: 'twitter:url', content: 'https://morecargo.ru/carriers' },
      {
        name: 'twitter:title',
        content:
          '6000 рублей за регистрацию? Легко! Используй бонусы для оплаты сервиса грузоперевозок'
      },
      {
        name: 'twitter:image',
        content: `https://morecargo.ru${socialImg}`
      }
    ]
  },
  components: {
    Header,
    StartScreen,
    Owners,
    PromoBlock,
    Steps,
    FAQ,
    Footer,
    BringFriend,
    Questions
  },
  data() {
    return {
      transparent: true,
      scrolled: 0,
      socialImg,
      mobileIconActive: false,
      carriersPage: true
    };
  },
  async asyncData() {
    const data = {
      faqList: []
    };

    try {
      const response = await axios.get(
        '/faq/get?category=carrier&title=UL&type=users'
      );
      data.faqList = response.content || [];
    } catch (error) {
      console.error(error);
    }

    return data;
  },
  created() {
    this.list = list;
    this.steps = steps;
  },
  mounted() {
    window.addEventListener('scroll', () => {
      this.scrolled = window.pageYOffset || document.documentElement.scrollTop;
      this.fixedHeader(this.scrolled);
    });
    if (this.$route.hash) {
      setTimeout(() => {
        location.href = this.$route.hash;
      }, 1);
    }
  },
  methods: {
    fixedHeader(num) {
      if (num > 0 || this.mobileIconActive === true) {
        this.transparent = false;
      } else {
        this.transparent = true;
      }
    }
  }
};
</script>

