  <section class="bring-friend">
    <div class="container">
      <a id="bring-friend" />
      <h2 class="main-title">
        <span class="text-gradient">Почему нужно регистрироваться сейчас?</span>
      </h2>
      <div class="item end">
        <LazyLoading
          url="/img/bring-1.jpg"
          alt="Подарок 5000 рублей на услуги сервиса при регистрации в MoreCargo"
          class="picture picture--left"
        />
        <div class="item__info py-0">
          <h3 class="item__title">
            <span class="text-gradient">Приветственный бонус</span>
            – {{ bonusPrivelege }} руб.
          </h3>
          <p class="item__text">
            Всем перевозчикам, зарегистрировавшимся в нашем сервисе, мы
            начисляем приветственный бонус. Вы сможете использовать его для
            оплаты услуг сервиса.
          </p>
          <div class="item__conditions">
            <template v-if="timerDates">
              <p class="item__conditions__text">
                До окончания действия акции «Льготная регистрация» и начала
                размещения заказов грузовладельцами осталось
              </p>
              <span class="item__conditions__timer">
                {{
                  !!timerDates.days ? getPlural('days', timerDates.days) : ''
                }}
                {{
                  !!timerDates.hours ? getPlural('hours', timerDates.hours) : ''
                }}
                {{
                  !!timerDates.minutes
                    ? getPlural('minutes', timerDates.minutes)
                    : ''
                }}
              </span>
            </template>
            <plain-button
              class="md-raised gradient"
              @click="showDialog('SignUp')"
            >
              Зарегистрироваться
            </plain-button>
          </div>
        </div>
      </div>
      <div class="item start">
        <div class="item__info">
          <h3 class="item__title">
            <span class="text-gradient">Программа</span>
            «Приведи друга»
          </h3>
          <p class="item__text">
            Приводите друзей и получайте по
