            рублей за каждого перевозчика, который зарегистрируется по вашей
            ссылке или промокоду и выполнит условия акции*.
          </p>
          <p class="footnote">
            * Вашему другу будет необходимо указать данные о себе или своей
            организации и верифицировать их.
          </p>
          <div class="item__conditions">
            <p class="item__conditions__text">
              Для ознакомления с деталями акций пройдите регистрацию в сервисе
            </p>
            <plain-button
              class="md-raised gradient"
              @click="showDialog('SignUp')"
