          class="picture picture--right"
        />
      </div>
    </div>
  </section>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { plural } from 'common/helpers/index';
import LazyLoading from 'common/components/LazyLoading';

const datesDeclensions = {
  years: ['год', 'года', 'лет'],
  months: ['месяц', 'месяца', 'месяцев'],
  days: ['день', 'дня', 'дней'],
  hours: ['час', 'часа', 'часов'],
  minutes: ['минута', 'минуты', 'минут']
};

let interval;

export default {
  components: {
    LazyLoading
  },
  data: () => ({
    timerDates: null
  }),
  computed: {
    ...mapState({
      bonusPrivelege: state => state.settings.bonusPrivelege,
      bonusReferrer: state => state.settings.bonusReferrer,
      startDateOfService: state => state.settings.startDateOfService
    })
  },
  watch: {
    startDateOfService() {
      if (this.startDateOfService) this.initTimer();
    }
  },
  mounted() {
    this.initTimer();
  },
  beforeDestroy() {
    clearInterval(interval);
  },
  methods: {
    ...mapMutations(['showDialog']),
    getPlural(type, number) {
      return plural(number, ...datesDeclensions[type]);
    },
    initTimer() {
      if (new Date(this.startDateOfService) > new Date()) {
        this.timerDates = this.getDates();
        interval = setInterval(() => {
          this.timerDates = this.getDates();
        }, 60000);
      }
    },
    getDates() {
      let remainder;
      const divider = 1000 * 3600 * 24;
      const a = new Date(this.startDateOfService) - new Date();
      const days = Math.floor(a / divider);
      remainder = a % divider;
      const hours = Math.floor(remainder / (1000 * 3600));
      remainder %= 1000 * 3600;
      const minutes = Math.floor(remainder / (1000 * 60));
      return { days, hours, minutes };
    }
  }
};
</script>

